<template>
    <div class="table-container">
        <table>
            <thead>
                <tr>
                    <th>Paper ID</th>
                    <th>Title</th>
                    <th>Type</th>
                    <!-- Add more <th> elements for each property you want to display -->
                </tr>
            </thead>
            <tbody>
                <tr v-for="paper in papers" :key="paper.paperID">
                    <td>
                        <a v-if="isClickable(paper.paperID)" @click="selectPaper(paper.paperID)" :class="{ 'clickable': true, 'selected': selectedId === paper.paperID }">
                            {{ paper.paperID }}
                        </a>
                        <span v-else>
                            {{ paper.paperID }}
                        </span>
                    </td>
                    <td>{{ paper.title }}</td>
                    <td>{{ paper.type }}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
  
<script>
import { read, utils } from 'xlsx';

export default {
    data() {
        return {
            papers: [],
            selectedId: null  // Add this
        }
    },
    methods: {
        isClickable(pdf) {
            return pdf !== 'tbd' && pdf !== 'book';
        },
        async processExcel() {
            const response = await fetch('./paper_list.xlsx');
            const arrayBuffer = await response.arrayBuffer();
            const data = new Uint8Array(arrayBuffer);
            const workbook = read(data, { type: 'array' });
            const worksheet = workbook.Sheets[workbook.SheetNames[0]];
            this.papers = utils.sheet_to_json(worksheet);
        },
        selectPaper(paperID) {
            this.selectedId = paperID;
            this.$emit('paper-select', paperID);
        },
    },
    created() {
        this.processExcel();
    }
}
</script>

<style>
.table-container {
  width: 100%;
  height: 90vh;  
  overflow: auto;  
}
table {
  min-width: 600px;  
}
.clickable {
  color: blue;
}

.selected {
  color: orange;
}
</style>